.modal {
  background: rgba(247, 243, 232, 0.82);
}
.modal .modal-content {
  border: none;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 6px;
}
.modal .close-modal {
  position: absolute;
  top: 0;
  right: 0;
}
.modal .close-modal:focus {
  box-shadow: none;
}
.modal .modal-title {
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 50px;
}

.success-modal {
  font-size: 24px;
  text-align: center;
}
.success-modal .modal-dialog {
  max-width: 715px;
}
.success-modal .success-wrap {
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 552px;
}
.success-modal .success-message-logo {
  width: 170px;
  margin-bottom: 40px;
}
.success-modal .success-message-title {
  text-transform: uppercase;
  margin-bottom: 30px;
}
.success-modal .success-message-order {
  margin-bottom: 40px;
}

@media (max-width: 767px) {
  .success-modal {
    font-size: 18px;
  }
  .success-modal .modal-content {
    min-height: 350px;
  }
}
.contacts-page {
  padding-bottom: 50px;
}
.contacts-page .contacts-wrap {
  padding: 40px 0;
}
.contacts-page .contacts-wrap .contact-item {
  margin-bottom: 40px;
}
.contacts-page .contacts-wrap .contact-item .contact-title {
  display: inline-block;
  font-weight: 700;
  margin-bottom: 20px;
}
.contacts-page .contacts-wrap .contact-item .contact-description {
  display: block;
  margin-bottom: 20px;
}
.contacts-page .contacts-wrap .contact-item .contact-description:last-child {
  margin-bottom: 0;
}

.feedback-form {
  background-color: #FCFCFC;
  padding: 40px 60px;
}
.feedback-form .form-group:nth-last-child(2) {
  margin-bottom: 40px;
}
.feedback-form .btn-wrap {
  text-align: right;
}
.feedback-form .submit {
  font-weight: 500;
  padding: 15px 40px;
  text-transform: uppercase;
}

.our-on-map .map {
  width: 100%;
}

#yaMap {
  height: 500px;
}
#yaMap .ymaps-2-1-78-map-copyrights-promo {
  display: none;
}

@media (max-width: 1199px) {
  .feedback-form {
    padding: 30px;
  }
}
@media (max-width: 991px) {
  .contacts-page .contacts-wrap {
    margin-top: 0;
    margin-bottom: 40px;
  }
}
@media (max-width: 575px) {
  #yaMap {
    height: 300px;
  }

  .feedback-form {
    padding: 30px 15px;
    margin: 0 -15px;
  }
  .feedback-form .submit {
    width: 100%;
  }
}